import Vue from "vue";
import Vuex from "vuex";
import Constant from "./Constant";
import http from "./http-common";
Vue.use(Vuex);
const store = new Vuex.Store({
  state: {
    QnaLists: [],
    Qna: {},
    CommentLists: [],
    comment: {},
    loginStatus: {}
  },
  actions: {
    [Constant.GET_QnaLIST]: store => {
      http
        .get("/qna")
        .then(response => {
          store.commit(Constant.GET_QnaLIST, { QnaLists: response.data.data });
        })
        .catch(exp => alert("게시물 조회에 실패하였습니다." + exp));
    },
    [Constant.GET_Qna]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .get("/qna/" + payload.no)
          .then(response => {
            console.log("조회하였습니다.");
            store.commit(Constant.GET_Qna, { Qna: response.data.data });
            resolve();
          })
          .catch(() => {
            console.log("조회에 실패하였습니다.");
            reject();
          });
      });
    },
    [Constant.REMOVE_Qna]: (store, payload) => {
      console.log("삭제!!!!!!!!" + payload.no);
      http
        .delete("/qna/" + payload.no)
        .then(() => {
          store.dispatch(Constant.GET_QnaLIST);
        })
        .catch(exp => alert("게시물 삭제에 실패하였습니다." + exp));
    },
    [Constant.SEARCH_Qna]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .get("/qna/list/" + payload.key + "_" + payload.word)
          .then(response => {
            console.log(
              payload.key + payload.word + "검색 결과로 조회하였습니다."
            );
            store.commit(Constant.SEARCH_Qna, { QnaLists: response.data.data });
            resolve();
          })
          .catch(() => {
            console.log("조회에 실패하였습니다.");
            reject();
          });
      });
    },
    [Constant.MODIEFY_Qna]: (store, payload) => {
      console.log(payload);
      http
        .put("/qna", {
          id: payload.Qna.id,
          no: payload.Qna.no,
          title: payload.Qna.title,
          contents: payload.Qna.contents,
          regdate: payload.Qna.regdate
        })
        .then(() => {
          //업데이트 하고 나서 다시 전체 리스트 뿌려주는 걸로
          store.dispatch(Constant.GET_QnaLIST);
        })
        .catch(exp => alert("수정 처리에 실패하였습니다." + exp));
    },
    [Constant.ADD_Qna]: (store, payload) => {
      http
        .post("/qna", {
          id: payload.Qna.id,
          title: payload.Qna.title,
          regdate: payload.Qna.regdate,
          contents: payload.Qna.contents
        })
        .then(() => {
          store.dispatch(Constant.GET_QnaLIST);
        })
        .catch(() => {
          console.log("추가에 실패하였습니다.");
        });
    },
    [Constant.GET_Comments]: (store, payload) => {
      return new Promise((resolve, reject) => {
        console.log("^^^^^^^^^");
        console.log(payload);
        http
          .get("/qna/comment/" + payload.qno)
          .then(response => {
            store.commit(Constant.GET_Comments, {
              CommentLists: response.data.data
            });
            resolve();
          })
          .catch(exp => {
            alert("댓글 조회에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.ADD_COMMENT]: (store, payload) => {
      return new Promise((resolve, reject) => {
        console.log("payload");
        console.log(payload);
        console.log("$$$$$$$$$$$$$$$$$$$$$$$$$");
        console.log(payload);

        http
          .post("/qna/comment", {
            id: payload.id,
            qno: payload.qno,
            comment: payload.comment
          })
          .then(() => {
            console.log("댓글추가완료");
            store.dispatch(Constant.GET_Comments, { qno: payload.qno });
            resolve();
          })
          .catch(() => {
            console.log("추가에 실패하였습니다.");
            reject();
          });
      });
    },
    [Constant.REMOVE_COMMENT]: (store, payLoad) => {
      return new Promise((resolve, reject) => {
        http
          .delete("/qna/comment/" + payLoad.cno)
          .then(() => {
            store.dispatch(Constant.GET_Comments, { qno: payLoad.qno });
            resolve();
          })
          .catch(exp => {
            alert("댓글 삭제에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.GET_LOGINSTATUS]: store => {
      http
        .get("/getLoginInfo.do")
        .then(response => {
          store.commit(Constant.GET_LOGINSTATUS, {
            loginStatus: response.data
          });
        })
        .catch(exp => alert("로그인정보 조회 처리에 실패하였습니다." + exp));
    }
  },

  mutations: {
    // 저장소에 데이터 실제 반영(commit시 호출됨.)
    [Constant.GET_QnaLIST]: (state, payLoad) => {
      state.QnaLists = payLoad.QnaLists;
    },
    [Constant.GET_Qna]: (state, payLoad) => {
      state.Qna = payLoad.Qna;
    },
    [Constant.GET_Comments]: (state, payLoad) => {
      state.CommentLists = payLoad.CommentLists;
    },
    [Constant.SEARCH_Qna]: (state, payLoad) => {
      state.QnaLists = payLoad.QnaLists;
    },
    [Constant.GET_LOGINSTATUS]: (state, payLoad) => {
      state.loginStatus = payLoad.loginStatus;
      console.log(state.loginStatus.id);
    }
  }
});

export default store;
