<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>QnA 리스트</h2>
    </div><div class="row" style="margin-top: 0px;margin-bottom: 20px;">
    <div class="searchCondition col-lg-4"></div>
    <div class="searchCondition col-lg-2">
      <div align="center">
        <h4 class="title">
          <a>검색조건 &nbsp;&nbsp;&nbsp;</a>
        </h4>
        <select id="searchCondition" size="1" class="form-control" name="key" v-model="key">
          <option value="all" selected>검색조건</option>
          <option value="id">아이디</option>
          <option value="title">제목</option>
          <option value="contents">내용</option>
        </select>
      </div>
    </div>
    <div class="searchWords col-lg-2">
      <div align="center">
        <h4 class="title">
          <a>검색단어 &nbsp;&nbsp;</a>
        </h4>
        <input type="text" class="form-control" id="searchWord" name="word" v-model="word" />
      </div>
    </div>

    <div class="searchButton col-lg-0.3">
      <div align="center">
        <h4 class="title">
          <a>&nbsp;&nbsp;&nbsp;</a>
        </h4>
        <input
          type="submit"
          id="searchButton"
          value="검색하기"
          class="btn btn-primary"
          @click="search"
          style="margin-top: 30px;width: 92px;height: 37px;padding-left: 15px;padding-right: 15px;"
        />
      </div>
    </div>
  </div>

  <table border="0" cellpadding="0" cellspacing="1" width="700" style="
    margin-left: 250px;
">
    <tr>
      <td width="100" align="center" bgcolor="#d5dae6" height="22">글 번호</td>
      <td width="100" align="center" bgcolor="#d5dae6" height="22">제목</td>
      <td width="100" align="center" bgcolor="#d5dae6" height="22">작성자</td>
      <td width="100" align="center" bgcolor="#d5dae6" height="22">작성일</td>
      <td width="100" align="center" bgcolor="#d5dae6" height="22"></td>
    </tr>
    <template v-for="qnaItem in QnaLists">
      <tr v-bind:key="qnaItem.no" @click="getQna(qnaItem.no)" align="center">
        <td width="100" align="center" height="22">{{ qnaItem.no }}</td>
        <td width="100" align="center" height="22">{{ qnaItem.title }}</td>
        <td width="100" align="center" height="22">{{ qnaItem.id }}</td>
        <td width="100" align="center" height="22">{{ qnaItem.regdate }}</td>
        <td>
          <!-- 자기 아이디로 작성한 글만 -->
          <template v-if="LoginStatus.status==true && LoginStatus.id ==  qnaItem.id">
            <span style="margin-right: 20px;" @click.stop="updateQna(qnaItem.no)">
              <i class="fas fa-pencil-alt"></i>
            </span>
            <span @click.stop="removeQna(qnaItem.no)">
              <i class="far fa-trash-alt"></i>
            </span>
          </template>
        </td>
      </tr>
    </template>
  </table>
  <div align="right" style="margin-right: 170px;">
    <input
      type="submit"
      id="searchButton"
      value="새로운 글 작성"
      class="btn btn-primary"
      @click="addQna"
      style="margin-bottom: 10px;margin-top: 20px;"
    />
  </div>
</template>

<script>
import Constant from "../Constant";

export default {
  name: "QnaList",
  data() {
    return {
      key: "",
      word: ""
    };
  },
  computed: {
    QnaLists() {
      return this.$store.state.QnaLists;
    },
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_QnaLIST);
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    updateQna(no) {
      this.$router.push("/qna/update/" + no);
    },
    getQna(no) {
      this.$router.push("/qna/detail/" + no);
    },
    removeQna(no) {
      this.$store.dispatch(Constant.REMOVE_Qna, { no });
    },
    search() {
      this.$store.dispatch(Constant.SEARCH_Qna, {
        key: this.key,
        word: this.word
      });
    },
    addQna() {
      this.$router.push("/qna/input");
    }
  }
};
</script>

<style scoped></style>
