<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>상세정보</h2>
    </div>

    <div align="center" style="
    width: 800px;
    margin-left: 200px;
    margin-top: 50px;
">
      <table class="table">
        <tr align="center">
          <td width="30%" style="color: #0c2e84; font-weight: bold;">아이디</td>
          <td>{{ Qna.id }}</td>
        </tr>
        <tr align="center">
          <td width="30%" style="color: #0c2e84; font-weight: bold;">번호</td>
          <td>{{ Qna.no }}</td>
        </tr>
        <tr align="center">
          <td width="30%" style="color: #0c2e84; font-weight: bold;">내용</td>
          <td>{{ Qna.contents }}</td>
        </tr>
        <tr align="center">
          <td width="30%" style="color: #0c2e84; font-weight: bold;">등록시간</td>
          <td>{{ Qna.regdate }}</td>
        </tr>
      </table>
    </div>

    <hr />

    <div align="right" style="
    margin-right: 100px;">
      <input
        type="submit"
        id="searchButton"
        value="목록으로 돌아가기"
        class="btn btn-primary"
        @click="showList"
        style="
    margin-top: 23px;
"
      />
    </div>
    <hr />
    <div align="center" style="
    margin-top: 20px;
">
      <input
        v-model="newComment.comment"
        placeholder="새로운 댓글 등록"
        style="width: 750px; align-items: center;"
      />
      <br />
      <input
        type="submit"
        id="searchButton"
        value="등록"
        class="btn btn-primary"
        @click="addComment(Qna.id, Qna.no)"
      />
    </div>
    <div>
      <table class="table" align="center" style="width: 800px; align-items: center;">
        <tr align="center">
          <td width="100">작성자</td>
          <td width="150">내용</td>
          <td width="100">작성일</td>
          <td width="100"></td>
        </tr>
        <template v-if="CommentLists[0]!=null">
          <tr v-for="comment in CommentLists" v-bind:key="comment.cno" align="center">
            <td width="100">{{ comment.id }}</td>
            <td width="150">{{ comment.comment }}</td>
            <td width="100">{{ comment.cregdate }}</td>
            <td width="100">
              <template v-if="LoginStatus.status==true && LoginStatus.id ==  comment.id">
                <span @click.stop="deleteComment(comment.cno)">
                  <i class="far fa-trash-alt"></i>
                </span>
              </template>
            </td>
          </tr>
        </template>
      </table>
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "detailpage",
  data() {
    return {
      Qna: {},
      CommentLists: [],
      newComment: {
        qno: 0,
        comment: "",
        id: ""
      }
    };
  },
  computed: {
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  mounted() {
    this.getComments();
    this.getQnalist();
  },
  created() {
    this.newComment.qno = this.$route.params.no;
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    getComments() {
      console.log("댓글들을 불러옵니다.");
      this.$store
        .dispatch(Constant.GET_Comments, { qno: this.newComment.qno })
        .then(() => {
          this.CommentLists = { ...this.$store.state.CommentLists };
          console.log(this.CommentLists[0]);
        });
    },
    getQnalist() {
      this.$store
        .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
        .then(() => {
          this.Qna = { ...this.$store.state.Qna };
        });
    },
    addComment(id, qno) {
      if (this.newComment.comment.trim() != "") {
        this.newComment.qno = qno;
        this.newComment.id = this.LoginStatus.id;
        console.log("id<<<<<" + qno);
        this.$store
          .dispatch(Constant.ADD_COMMENT, {
            qno: this.newComment.qno,
            id: this.newComment.id,
            comment: this.newComment.comment
          })
          .then(() => {
            this.getComments();
            this.clear();
          });
      }
    },
    deleteComment(no) {
      var cno = no;
      this.$store
        .dispatch(Constant.REMOVE_COMMENT, { cno: cno, qno: this.Qna.no })
        .then(() => {
          this.getComments();
        });
    },
    clear() {
      this.newComment.comment = "";
    },
    showList() {
      this.$router.push("/qna/list");
    }
  }
};
</script>

<style scoped></style>
