<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>등록하기</h2>
    </div>

    <table class="table">
      <tr>
        <td width="30%" style="color: #0c2e84; font-weight: bold;">제목</td>
        <td>
          <input
            type="title"
            class="form-control col-lg-8"
            name="title"
            placeholder="제목"
            v-model="Qna.title"
          />
        </td>
      </tr>
      <tr>
        <td width="30%" style="color: #0c2e84; font-weight: bold;">아이디</td>
        <td>{{LoginStatus.id}}</td>
      </tr>
      <tr>
        <td width="30%" style="color: #0c2e84; font-weight: bold;">내용</td>
        <td>
          <input
            type="contents"
            class="form-control col-lg-8"
            name="contents"
            placeholder="내용"
            v-model="Qna.contents"
          />
        </td>
      </tr>
    </table>

    <div align="center">
      <input type="button" @click="addTodo" class="btn btn-primary" value="등록하기" />
      <input type="button" value="목록으로 돌아가기" class="btn btn-primary" @click="showList" />
    </div>
  </div>
</template>
<script>
import Constant from "../Constant";
export default {
  data() {
    return {
      Qna: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      }
    };
  },
  computed: {
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    addTodo() {
      if (this.Qna.contents.trim() != "") {
        this.Qna.id = this.LoginStatus.id;
        console.log(this.Qna);
        this.$store.dispatch(Constant.ADD_Qna, { Qna: this.Qna });
        this.$router.push("/qna/list");
        this.clear();
      }
    },
    clear() {
      (this.Qna.title = ""), (this.Qna.regdate = ""), (this.Qna.contents = "");
    },
    showList() {
      this.$router.push("/qna/list");
    }
  }
};
</script>

