<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>수정하기</h2>
    </div>
    <div class="row marginbox">
      <h4 class="col-lg-4" align="center">제목</h4>
      <input
        type="title"
        class="form-control col-lg-8"
        name="title"
        placeholder="제목"
        v-model="Qna.title"
      />
    </div>
    <div class="row marginbox">
      <h4 class="col-lg-4" align="center">내용</h4>
      <input
        type="contents"
        class="form-control col-lg-8"
        name="contents"
        placeholder="내용"
        v-model="Qna.contents"
      />
    </div>
    <div align="center">
      <input
        type="button"
        @click="updateTodo"
        class="form-control col-lg-1 btn btn-primary"
        id="editButton"
        value="수정하기"
        style="
    padding-bottom: 0px;
    padding-right: 10px;
    padding-top: 0px;
    padding-left: 10px;
"
      />
      <input
        type="submit"
        id="searchButton"
        value="목록으로 돌아가기"
        class="btn btn-primary"
        @click="showList"
        style="margin-top: 0px;"
      />
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "updatepage",
  data() {
    return {
      Qna: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      }
    };
  },
  created() {
    this.$store
      .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
      .then(() => {
        this.Qna = { ...this.$store.state.Qna };
      });
  },
  methods: {
    updateTodo() {
      console.log(this.Qna);
      this.$store.dispatch(Constant.MODIEFY_Qna, { Qna: this.Qna });
      this.$router.push("/qna/list");
    },
    showList() {
      this.$router.push("/qna/list");
    }
  }
};
</script>

